\n                                >\n                                <input\n                                    type=\"file\"\n                                    id=\"userProfile\"\n                                    class=\"d-none\"\n                                    @change=\"changeProfile\"\n                                />\n                            </div>\n                        </div>\n                        <div class=\"card-body px-5\">\n                            <h3>Account</h3>\n                            <form>\n                                <div class=\"row my-5\">\n                                    <label for=\"name\" class=\"col-4\"\n                                    >Username\n                                        <span class=\"fs-5\" style=\"color: #e73862\">\n                                *</span\n                                        ></label\n                                    >\n                                    <input\n                                        v-model=\"username\"\n                                        type=\"text\"\n                                        id=\"name\"\n                                        class=\"col-8 px-3 py-1\"\n                                    />\n                                </div>\n                                <div class=\"row my-5\">\n                                    <label for=\"email\" class=\"col-4\"\n                                    >Email\n                                        <span class=\"fs-5\" style=\"color: #e73862\">\n                                *</span\n                                        ></label\n                                    >\n                                    <input\n                                        v-model=\"email\"\n                                        type=\"email\"\n                                        id=\"email\"\n                                        class=\"col-8 px-3 py-1\"\n                                    />\n                                </div>\n                                <div class=\"row my-5\">\n                                    <label for=\"password\" class=\"col-4\"\n                                    >Password\n                                        <span class=\"fs-5\" style=\"color: #e73862\">\n                                *</span\n                                        ></label\n                                    >\n                                    <input\n                                        v-model=\"password\"\n                                        type=\"password\"\n                                        id=\"password\"\n                                        class=\"col-8 px-3 py-1\"\n                                        placeholder=\"\"\n                                    />\n                                </div>\n                                <div class=\"row my-5\">\n                                    <label for=\"profile\" class=\"col-4\"\n                                    >Profile\n                                        <span class=\"fs-5\" style=\"color: #e73862\">\n                                *</span\n                                        ></label\n                                    >\n                                    <label for=\"profile\" class=\"btn btn-outline-dark col-8\"\n                                    ><i class=\"fa-solid fa-upload\"></i> Choose\n                                        Profile</label\n                                    >\n                                    <input\n                                        type=\"file\"\n                                        id=\"profile\"\n                                        class=\"d-none\"\n                                        @change=\"setProfile\"\n                                    />\n                                </div>\n\n                                <div class=\"row my-5\">\n                                    <button\n                                        type=\"button\"\n                                        class=\"btn btn-primary\"\n                                        data-bs-toggle=\"modal\"\n                                        data-bs-target=\"#confirmAction\"\n                                        :class=\"{ disabled: loading }\"\n                                        style=\"color: white\"\n                                    >\n                                        Save Changes\n                                    </button>\n                                    <Model\n                                        title=\"Reviews and confirm changes\"\n                                    id=\"confirmAction\"\n                                        :description=\"`<p>The following changes have been made:</p>\n                        <p>Username : <span>${username}</span>.</p>\n                        <p>Email    : ${email}.</p>\n                        <p>Password : ${\n                            password ? password : 'No changes'\n                        }.</p>\n                        Are you sure you want to submit?`\"\n                                        v-on:confirm=\"updateInfos\"\n                                    />\n                                </div>\n                            </form>\n                        </div>\n                    </figure>\n                </div>\n\n            </div>\n        </div>\n    </Main>\n</template>\n<script>\nimport axios from \"axios\";\nimport Model from \"./Model.vue\";\nimport Main from \"../dashboard/Layout/Main\";\nimport Breadcrumb from \"../layouts/Breadcrumb\";\nexport default {\n    data() {\n        return {\n            username: \"\",\n            email: \"\",\n            password: \"\",\n            profile: \"\",\n            role: \"\",\n            userprofile: \"\",\n            loading: false,\n            confirm: false,\n        };\n    },\n    created(){\n        this.$Progress.start();\n        this.role = this.$store.state.auth.role;\n    },\n    mounted(){\n        this.$Progress.finish();\n    },\n    components: {Breadcrumb, Main, Model },\n    computed: {\n        user() {\n            const user = this.$store.state.auth.user;\n            this.username = user.name;\n            this.email = user.email;\n            this.userprofile = user.profile;\n            return user;\n        },\n    },\n    methods: {\n        changeProfile(e) {\n            this.profile = e.target.files[0];\n\n            this.updateInfos();\n        },\n        setProfile(e) {\n            this.profile = e.target.files[0];\n        },\n        async updateInfos() {\n            this.loading = true;\n            const formData = new FormData();\n            const user_id = this.$store.state.auth.user.id;\n            formData.append(\"password\", this.password);\n            formData.append(\"name\", this.username);\n            formData.append(\"email\", this.email);\n            formData.append(\"profile\", this.profile);\n            formData.append(\"role\", this.role);\n            const res = await axios.post(\n                \"/api/dashboard/admin/\" + user_id,\n                formData,\n                {\n                    headers: {\n                        Authorization: `Bearer ${this.$store.state.auth.token}`,\n                    },\n                }\n            );\n            const { success, data } = res.data;\n            if (success) {\n                this.loading = false;\n                this.$store.dispatch(\"getVerification\");\n            } else {\n                this.loading = false;\n            }\n        },\n    },\n};\n</script>\n<style scoped>\n.card {\n    min-width: 400px;\n}\n.profile {\n    width: 200px;\n    height: 200px;\n    border-radius: 50%;\n}\n\ninput {\n    border: 1px solid gray;\n    border-radius: 10px;\n}\n</style>\n"],"sourceRoot":""}]);
